import React, { Fragment } from 'react';
import classes from './Tablet.module.css'
import { FaTabletAlt } from 'react-icons/fa';

const Tablet = (props) => {
    const isTablet = props.width <= 1200 && props.width > 500;
    return (
        <Fragment>
            <div className={classes.Tablet}>
                {
                    isTablet ?
                        <div>
                            <label>Device: &nbsp;</label>
                            <b>Tablet</b>
                            <div>
                                <FaTabletAlt 
                                    className={classes.TabletIcon}
                                />
                            </div>
                        </div>
                        :
                        null
                }
            </div>
        </Fragment>
    );
};

export default Tablet;