import React, { useEffect, useState, Fragment } from 'react';
import Desktop from './Desktop/Desktop';
import Mobile from './Mobile/Mobile';
import Tablet from './Tablet/Tablet';
import DisplayCurrentWidth from './DisplayCurrentWidth/DisplayCurrentWidth';
import Button from './Button/Button';

const DisplayDevice = () => {

    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [isVisibleMyComponent, setVisibility] = useState(false)

    useEffect(() => {
        const captureWindowResizeEvent = () => {
            const currentWidth = window.innerWidth;
            setWindowWidth(currentWidth);
        }
        window.addEventListener("resize", captureWindowResizeEvent)
        return () => { window.removeEventListener("resize", captureWindowResizeEvent) }
    }, []);

    return (
        <Fragment>
            <div>
                <Button
                    clicked={() => setVisibility(!isVisibleMyComponent)}>
                    CLICK HERE TO SEE CURRENT WIDTH
                </Button>
                {
                    isVisibleMyComponent &&
                    <DisplayCurrentWidth
                        width={windowWidth}
                    />
                }
                <Mobile
                    width={windowWidth}
                />
                <Tablet
                    width={windowWidth}
                />
                <Desktop
                    width={windowWidth}
                />
            </div>
        </Fragment>
    );
};

export default DisplayDevice;

