import React, { Fragment } from 'react';
import classes from './DisplayCurrentWidth.module.css';

const DisplayCurrentWidth = (props) => {
    const isHeader = props.width > 500;
    return (
        <Fragment>
            <div className={classes.DisplayLabel}>
                {
                    isHeader ?
                        <div>
                            <label>Welcome to the Device Width Checker.</label>
                        </div>
                        :
                        null
                }
                <div>
                    <label>Current Width:&nbsp;</label>
                    <strong>{props.width}</strong>
                </div>
            </div>
        </Fragment>
    );
};

export default DisplayCurrentWidth;